import Vue from 'vue';
import Vuex from 'vuex';

import axios from 'axios'
/*import * as actions from './actions';  
 // * 表示将 './actions'模块中的所有接口挂载到actions对象上  as 表示别名的意思
import * as mutations from './mutations';
import * as getters from './getters';
import state from './rootState';*/
Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    count: 0,
    platform:1,
    amount1:5,
    todos:[
    	{id:1,text:'1',done:true},
    	{id:2,text:'2',done:false},
    	{id:3,text:'3',done:true},
    	{id:4,text:'4',done:true},
    ],
    nowWeather:{},
    city:'北京',
  },
  mutations: {
    INC: function(state,payload){
    	state.count += payload.amount;
    },
    DEC: state => state.count--,
    INCREMENT(state) {
        state.count +=state.amount1;
    },
    saveFrom (){
    	axios.get('https://free-api.heweather.com/s6/weather/now?location=北京&key=c861f54eeb0c49a6a73ebf46f6d14ccf')
    	.then(function(res){
    		this.state.nowWeather = res.data.HeWeather6[0]
    	}.bind(this))
    	.catch(function(err){
    		console.log(err)
    	})
    }
  },
  getters:{
  	doneTodos: state =>{
  		return state.todos.filter(todo => todo.done)
  	}
  },
  actions:{
  	increment (context){
  		context.commit('INCREMENT')
  	},
    incrementAsync ({commit}){
    	setTimeout(() => {
    		commit('INCREMENT')
    	},1000)
    },
    checkout({commit,state},products){
    	const savedCartItems = [...state.cart.added]
    	commit(types.CHECKOUT_REQUEST)
    	shop.buyProducts(
    		products,
    		() => commit (types.CHECKOUT_SUCCESS),
    		() => commit (types.CHECKOUT_FAILURE,savedCartItems)
    	)
    }
  },
  getters:{
	doneTodos:state =>{return state.todos.filter(todo => todo.done)} 
}
	  
})
export default store;




