<template>
  <div class="hello">
    <p>demo{{count}}
    <button @click="inc">+</button>
    <button @click="dec">-</button>
  </p>
  <p v-if="nowWeather.basic">{{nowWeather.basic.parent_city}}</p>
  </div> 
</template>

<script>
export default{
  data(){
    return{
      
    }
  },
  computed: {
    count () {
      return this.$store.state.count
    },
    nowWeather () {
      return this.$store.state.nowWeather
    }
  },
  created (){
    this.saveFrom()
  },
  methods: {
    inc () {
        this.$store.commit({
          type:'INC',
          amount:10
        })
    },
    dec () {
        this.$store.commit('DEC')
    },
    incrementAsync(){
        this.$store.dispatch('incrementAsync');
    },
    saveFrom(){
        this.$store.commit('saveFrom')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



