<template>
  <div class="article-share-block">
      <p class="label">share{{platform}}</p>
      <ul>
        <li v-for="item in todos">
          {{item.done}}
        </li>
      </ul>
      <p>{{city}}</p>
  </div>
</template>

<script>
export default {
  data () {
    return {

    }
  },
  computed: {
    count () {
        return this.$store.state.count
    },
    platform () {
        return this.$store.state.platformpal
    },
    todos () {
        return this.$store.getters.doneTodos
    },
     city () {
      return this.$store.state.city
    }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



